<template>
	<view class="container">
		<uni-forms ref="baseForm" :modelValue="formData">
			<uni-forms-item>
				<uni-easyinput prefixIcon="email" v-model="formData.email" placeholder="请输入邮箱" />
			</uni-forms-item>
		</uni-forms>
		<button @click="setEmail">保存</button>
	</view>
</template>

<script>
	import {
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				formData: {
					email: ''
				},
			}
		},
		onLoad(options) {
			this.formData.email = options.email == 'undefined' ? '' : options.email
		},
		methods: {
			...mapMutations(['setUserInfo']),
			async setEmail() { //修改邮箱
			    let userInfo = uni.getStorageSync('userInfo')
				if(this.formData.email == userInfo.email){
					uni.navigateBack()
				}else{
					let data = {
						userId: userInfo.id,
						email: this.formData.email
					}
					const response = await this.$api.user.setEmail(data)
					if (response.data.code == 200) {
						userInfo.email = data.email
						this.setUserInfo(userInfo)
						uni.showToast({
							title: response.data.message,
							icon: 'success',
							duration: 2000
						});
						let t = setTimeout(res => {
							uni.navigateBack()
							clearTimeout(t)
						}, 500)
					}
				}
			}
		}
	}
</script>

<style lang="scss">
	.container {
		background-color: #fff;
		width: 100%;
		height: 100vh;
		padding: 80upx 50upx 0;

		/deep/.is-input-border {
			border-color: rgb(229, 229, 229) !important;
		}

		/deep/.uniui-clear {
			color: rgb(192, 196, 204) !important;
		}

		/deep/.uni-easyinput__content-input {
			height: 70upx;
		}

		button {
			background-color: #009000;
			color: #fff;

		}
	}
</style>